import styled from "styled-components"
import MyCard from './CardFolder/Card'

export default function Protfolio() {
    let Projects = [
        {
            imgUrl: "/portfolio/MoviesPhoto.png", WebUrl: "https://asalef10.github.io/moviesApp/", textPortfolio: "A movie site I built that gets what api"
        }, {
            imgUrl: "/portfolio/NewsAsi.png", WebUrl: "https://asalef10.github.io/AsiNews/", textPortfolio: "A news site built in the shape of a grid"

        },
        {
            imgUrl: "/portfolio/ApleWeb.png", WebUrl: "https://asalef10.github.io/apple/", textPortfolio: "Apple website"
        }, {
            imgUrl: "/portfolio/HotelWeb.png", WebUrl: "https://asalef10.github.io/hotel/", textPortfolio: "Website of a hotel in Dubai built in the shape of a grid"
        }, {
            imgUrl: "/portfolio/IsracrtWeb.png", WebUrl: "https://asalef10.github.io/israCard/", textPortfolio: "IsraCard Website "
        }, {
            imgUrl: "/portfolio/WebDeveloper.png", WebUrl: "https://asalef10.github.io/fullstack-developer/", textPortfolio: "Website About Me"
        }
    ]

    const Myposition = styled.div`
    margin-left: 300px;
    text-align: center;
    `
    return (
        <Myposition>
            <h4>Projects I made myself are welcome to see and be impressed.</h4>
            <div style={{ display: "flex", flexWrap: "wrap", justifyContent: "space-between", padding: "27px" }}>
                {
                    Projects.map((project) => {
                        return <MyCard imgUrl={project.imgUrl} WebUrl={project.WebUrl} textPortfolio={project.textPortfolio}></MyCard>
                    })
                }
            </div>
        </Myposition>
    )
}