import { useEffect, useRef } from "react"
import styled from "styled-components"
export default function Contact() {
    const firstName=useRef()
    const Myposition = styled.div`
    margin-left: 300px;
    `

    useEffect(()=>{
        firstName.current.focus()
    },[])
    return (
        <>
            <Myposition>
                <form action="https://formsubmit.co/Asalef10@gmail.com" method="POST">
                    <div class="row">
                        <div class="col">
                            <input ref={firstName} name="firsName" type="text" class="form-control" placeholder="First name" />
                        </div>
                        <div class="col">
                            <input name="LastName" type="text" class="form-control" placeholder="Last name" />
                        </div>
                        <div class="col">
                            <input name="Email" type="email" class="form-control" placeholder="Email" />
                        </div>
                        <div class="col">
                            <textarea name="text" id="" cols="30" rows="10" placeholder="Massage"></textarea>
                        </div>
                    </div>
                    <button onClick={()=>{alert("Thank you we will get back to you soon")}} type="submit">send</button>
                </form>
            </Myposition>
        </>
    )
}