import styled, { keyframes } from 'styled-components';
import Typical from 'react-typical';
import { useState } from 'react';
import Card from '../Protfolio/CardFolder/Card';
export default function HomePageC() {
  let ArryCard = [
    {
      imgUrl: '/portfolio/IMG_4702.PNG',
      textPortfolio:
        'In the picture show me explaining in class about the javaScript engine And The Hook UseRef',
    },
    {
      imgUrl: '/portfolio/barZik.png',
      textPortfolio:
        'Ren Bar-Zik, the well-known programmer in the lecture, is a fascinating lecture for us students.',
    },
  ];

  const [DetilsAboutMe, setDetilsAboutMe] = useState('');
  const DivPhoto = styled.div`
    margin-left: 300px;
    margin-top: -10px;
  `;

  const AboutMeOne = styled.div`
    display: flex;
    justify-content: flex-end;
  `;

  const DivGust = styled.div`
    background-color: #000000;
    border: 1px solid black;
    opacity: 0.6;
    height: inherit;
  `;
  const MyText = keyframes`
      0% {
          
      transform: (0deg);
    }
  
    100% {
      transform: rotate(360deg);
    }
      `;
  const Text = styled.h1`
    margin-top: 0;
    height: fit-content;
    color: white;
    height: fit-content;
    background-color: black;
  `;
  const BackGroundPhoto = styled.div`
    background-size: 100% 100%;
    width: 100%;
    height: 700px;
    background-image: url('/portfolio/IMG_4770.jpg');
    background-attachment: fixed;
    background-position-x: 300px;
  `;
  const AboutMe = styled.div`
    width: inherit;
    display: flex;
    background-color: #f0f8ff99;
    /* justify-content: space-between; */
  `;

  const DivLeft = styled.div`
    display: flex;
    justify-content: end;
  `;

  function ScrolloToDwon() {
    window.scrollTo({
      top: 750,
      bottom: 750,
      behavior: 'smooth',
    });
  }

  const [NumShow, setNumShow] = useState('Read');
  function myDetilsAboutMe() {
    if (NumShow == 'Read') {
      setDetilsAboutMe(
        '  2020 I started a full-developer bootcamp course during which I acquired knowledge on the client side and on the server side   Character traits on me   Has good interpersonal skills, a high sense of service and the ability to handle several tasks simultaneously. Highly self-disciplined and motivated, responsible and punctual'
      );
      setNumShow('Hide');
    } else if (NumShow == 'Hide') {
      setDetilsAboutMe('');
      setNumShow('Read');
    }
  }
  return (
    <>
      <DivPhoto>
        <BackGroundPhoto>
          <DivGust>
            <img
              width="5%"
              className="animate__adnimated animate__bounce"
              onClick={ScrolloToDwon}
              style={{ backgroundColor: 'black' }}
              src="https://ak.picdn.net/shutterstock/videos/1067217055/thumb/1.jpg?ip=x480"
            ></img>
            <div
              style={{
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'items',
                height: 'inherit',
              }}
            >
              <Text className="animate__animated animate__rotateInDownRight">
                Asalef Alena
                <Typical
                  steps={['  Developer Web', 1000, '', 500]}
                  loop={Infinity}
                  wrapper="p"
                />{' '}
              </Text>
            </div>
          </DivGust>
        </BackGroundPhoto>
        <h1 onClick={myDetilsAboutMe} style={{ textAlign: 'center' }}>
          About Me:{' '}
        </h1>
        <button onClick={myDetilsAboutMe} type="button" class="btn btn-primary">
          {NumShow}
        </button>
        <AboutMeOne>
          <h5 style={{ textAlign: 'center' }}>{DetilsAboutMe}</h5>
        </AboutMeOne>
        <AboutMe>
          <img
            width="20%"
            style={{ boxShadow: '16px 1px 15px black' }}
            src="/portfolio/asiPhoto.jpg"
          ></img>
          <div style={{ width: '50%' }}>
            <div class="card mb-3" style={{ maxWidth: '540px' }}>
              <div class="row g-0">
                <div class="col-md-4"></div>
                <div class="col-md-8">
                  <div class="card-body">
                    <h5 class="card-title"> Web Developer</h5>
                    <p class="card-text">
                      <ul>
                        <li>
                          Name: <b> Asalef</b>
                        </li>
                        <li>
                          LastName:<b>Alena </b>{' '}
                        </li>
                        <li>
                          Birthday:<b>13/11/1999 </b>
                        </li>
                      </ul>
                    </p>
                  </div>
                </div>{' '}
              </div>{' '}
            </div>{' '}
          </div>
        </AboutMe>

        <div
          style={{
            display: 'flex',
            flexWrap: 'wrap',
            justifyContent: 'space-evenly',
            padding: '27px',
          }}
        >
          {ArryCard.map((element) => {
            return (
              <Card
                imgUrl={element.imgUrl}
                textPortfolio={element.textPortfolio}
              ></Card>
            );
          })}
        </div>
      </DivPhoto>
    </>
  );
}
