import HomePageC from '../Component/Pages/HomePage/HomePage'
import { Switch, Route, Link } from 'react-router-dom'
import Skils from '../Component/Pages/SkilsPage/SkilsPage'
import Protfolio from '../Component/Pages/Protfolio/Portfolio'
import Contact from '../Component/Pages/Contact/Contact'
export default function AppRouter() {
    return (
        <>

            <Switch>
                <Route exact path="/">
                    <HomePageC />
                </Route>
                <Route path="/SkilsPage">
                    <Skils />
                </Route>
                <Route path="/Portfolio">
                    <Protfolio />
                </Route>
                <Route path="/Contact">
                    <Contact />
                </Route>
            </Switch>
        </>
    )
}